import React from 'react';
import { Link } from 'gatsby';
import Layout from '../components/layout';
import { useWishlist } from '../context/WishlistContext';

const WishlistPage = () => {
  const { wishlistItems, removeFromWishlist } = useWishlist();

  return (
    <Layout>
      <div className="container py-5">
        <h1 className="display-4 mb-4">Wishlist</h1>
        {wishlistItems.length === 0 ? (
          <p>Your wishlist is empty.</p>
        ) : (
          <div className="row">
            {wishlistItems.map(item => (
              <div key={item.id} className="col-md-4 mb-4">
                <div className="card h-100">
                  {item.image?.childImageSharp ? (
                    <img
                      className="card-img-top"
                      src={item.image.childImageSharp.gatsbyImageData.images.fallback.src}
                      alt={item.name}
                    />
                  ) : (
                    <p>No image available</p>
                  )}
                  <div className="card-body">
                    <h5 className="card-title">{item.name}</h5>
                    <p className="card-text">${item.price}</p>
                    <Link to={`/products/${item.id}`} className="btn btn-primary">View Details</Link>
                    <button className="btn btn-secondary ml-2" onClick={() => removeFromWishlist(item)}>Remove from Wishlist</button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </Layout>
  );
};

export default WishlistPage;
